.full-width {
  width: 1200px;
  position: relative;
}

.fix-ratio-106 {
  padding-bottom: 106.666666%;
}

.clearfix {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.text-left {
  text-align: left !important;
}

.text-center {
  text-align: center !important;
}

.text-right {
  text-align: right !important;
}

.v-align-middle {
  vertical-align: middle !important;
}

.no-scroll {
  overflow: hidden;
}

@media only screen and (min-width : 1200px) {
  .full-width {
    width: 100vw;
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
  }
}