.overlay-loader-bg {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  background-color: rgba(0, 0, 0, .15);

  .hint-text,
  .progress {
    width: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -10px;
    margin-left: -100px;
  }

  .progress-circle-lg-white {
    background-image: url("/_ui/img/progress/progress-circle-lg-white.svg");
    width: 50px;
    height: 50px;
    margin: 10px auto;
  }

  .hint-text {
    text-align: center;
    margin-top: -35px;
    color: #fff;
    font-size: 13px;
  }
}
