/** common **/
.table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;

  thead {
    tr {
      th {
        font-size: 13px;
        color: #898989;
        text-align: center;
      }
    }
  }

  tbody {
    tr {
      td {
        background: #fff;
        border-top: 1px solid rgba(230, 230, 230, 0.7);
        padding: 20px 0 0;
        font-size: 13px;
        vertical-align: middle;
        text-align: center;
        &.thumb {
          padding: 20px 0;
        }
      }

      &.no-border-top {
        td {
          border-top: none;
        }
      }

      &:first-child {
        td {
          border-top: none;
        }
      }
    }
  }
}

.table-full-width-msg {
  padding: 50px 0;
  text-align: center;
  color: #898989;
}

/** order table **/
.table-order-desktop {
  .thumb {
    vertical-align: top;
    a {
      display: table-cell;
      position: relative;
      width: 100px;
      height: 150px;
      vertical-align: middle;
      &::before {
        content: '';
        position: absolute;
        display: block;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: #000;
        opacity: .03;
        z-index: 1;
      }
    }
    img {
      width: 100px;
      line-height: 1.3;
      color: #1f1f1f;
    }
  }

  tbody {
    tr.set-items {
      &.hide {
        display: none;
      }
      td {
        border-top: none;
      }
      .thumb {
        padding-left: 30px;

        &.no-padding {
          padding-left: 0;

          .checkbox {
            margin-top: 60px;
            margin-right: 23px;
            display: block;
            float: left;
          }

          a {
            float: left;
          }
        }
      }
      .product {
        padding-left: 30px;
      }
    }
  }

  .product {
    position: relative;
    vertical-align: top;
    background: none;
    h4 {
      font-family: 'helvetica';
      font-size: 11px;
      font-weight: 900;
      margin: 10px 0;
      small {
        font-weight: 500;
        color: #aaa;
        &::before {
          content: '';
          display: inline-block;
          width: 1px;
          height: 8px;
          margin: 0 8px;
          background: #f1f1f1;
        }
        &.separater-none {
          &::before {
            display: none;
          }
        }
      }
    }

    p {
      font-size: 16px;
      line-height: 1.3;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      width: 335px;
      margin-bottom: 21px;
    }

    a {
      font-size: 15px;
      color: #1f1f1f;
    }

    .meta {
      margin-bottom: 23px;
      li {
        color: #898989;
        display: inline-block;
        min-width: 76px;
        &.amount {
          span {
            margin-left: 5px;
          }
        }
        &.prod-price {
          display: block;
          margin-top: 10px;
        }
      }
    }

    .options {
      .btn-option {
        position: absolute;
        bottom: 25px;
        color: #1f1f1f;
        font-size: 12px;
        padding-right: 15px;
        background: url(/_ui/img/pc/icn/icn-arrow-open.png) no-repeat 100% 50%;
        &.set-open {
          background: url(/_ui/img/pc/icn/icn-arrow-close.png) no-repeat 100% 50%;
        }
        &.next {
          background: url(/_ui/img/pc/icn/icn-arrow-right.png) no-repeat 100% 75%;
          padding-right: 12px;
          padding-top: 3px;
        }
      }
    }
  }

  .price {
    line-height: 1.6;

    del {
      display: block;
      color: #aaa;
    }

    .btn {
      margin: 0 auto;
      height: 26px;
      line-height: 26px;
      padding: 0 10px;
      margin-top: 4px;
    }

    .family-sale-on {
      background-color: #f23d4e;
    }

    .family-sale-off {
      background-color: #bbb;
    }

    .family {
      display: block;
      color: #f23d4e;
      padding: 4px 0;
    }
  }

  .delivery {
    line-height: 150%;
    .pickup-info {
      margin-top: 5px;
      letter-spacing: -.4px;
      dt {
        font-size: 13px;
        color: #1f1f1f;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: normal;
        line-height: 19px;
        text-align: center;
        word-wrap: break-word;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        a {
          font-size: 13px;
          color: #1f1f1f;
          text-decoration: underline;
        }
      }
      dd {
        margin-top: 4px;
        font-size: 13px;
        color: #898989;
        text-align: center;
        line-height: 18px;
      }
    }
  }

  .state {
    line-height: 1.3846;
    .btn {
      font-size: 12px;
      width: 100%;
      margin-top: 5px;
      padding: 0;
      &:first-child {
        margin-top: 0;
      }
    }
    .state-rma {
      color: #ff2020;
    }
  }

  .reason {
    td {
      padding: 0 0 20px;
      border: none;
      text-align: left;
      span {
        font-size: 15px;
        color: #1f1f1f;
        &.detail {
          font-size: 15px;
          color: #898989;
          line-height: 1.4;
        }
      }
    }
  }
}
